.section_page.gallery{
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      border-bottom: solid 3rem var(--dark-400);
      gap: 1rem;
}

.card_gallery{
      width: 100%;
      height: 200px;
      background: var(--dark-300);
      position: relative;
      border-radius: .6rem;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
}

.card_gallery img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
      transition: .2s;
      transform: scale(1);
}

.card_gallery:hover img{
      transform: scale(1.3);
}

.card_gallery button{
      width: 60px;
      height: 60px;
      position: absolute;
      border-radius: 50%;
      background: var(--alpha);
      opacity: 0;
      transform: scale(0);
      transition: .2s;
}

.card_gallery button *{
      font-size: 2rem;
      color: var(--light);
}

.card_gallery:hover button{
      opacity: 1;
      transform: scale(1);
}