.content{
      width: 720px;
      height: 760px;
      background: var(--dark-400);
      display: inline-block;
      position: relative;
}

.header_content{
      width: 100%;
      height: 105px;
}

.header_content__menu ul{
      width: 100%;
      height: 30px;
      display: flex;
      align-items: center;
}

.header_content__menu ul li{
      width: 25%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      padding-left: 1.5rem;
      cursor: pointer;
}

.header_content__menu ul li span{
      text-transform: uppercase;
      font-weight: 600;
      font-size: 1.1rem;
      color: var(--dark-100);
}

.header_content__menu ul li.active{
      width: 210px;
      background: var(--dark-300);
      border-radius: .6rem .6rem 0 0;
}

.header_content__menu ul li.active span{
      color: transparent;
      background: var(--gradient-45);
      -webkit-background-clip: text;
      -moz-background-clip: text;
      background-clip: text;
}

.header_content_status{
      width: 100%;
      height: 75px;
      padding: 3rem;
      background: var(--dark-300);
      display: flex;
      align-items: center;
      gap: 3rem;
      justify-content: space-between;
}

.status_nav__arrow{
      width: 88px;
      height: 38px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-shrink: 0;
}

.status_nav__arrow button{
      width: 38px;
      height: 38px;
      background: var(--dark-400);
      border-radius: 50%;
}

.status_nav__arrow button *{
      font-size: 2rem;
      color: var(--dark-200);
}

.header_status_title{
      flex: 1;
      height: 38px;
      background: var(--dark-400);
      display: flex;
      align-items: center;
      padding-left: 2rem;
      border-radius: .4rem;
}

.header_status_title h2{
      font-size: 1.5rem;
      font-weight: 500;
}

.header_status__online{
      width: 130px;
      height: 38px;
      flex-shrink: 0;
}

.pages{
      width: 100%;
      height: 655px;
}

.footer_content{
      width: 440px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      bottom: 0;
      left: calc(50% - 220px);
      background: var(--dark-300);
      border-radius: .6rem .6rem 0 0;
      z-index: 2;
}

.footer_content p{
      color: var(--dark-100);
      font-size: 1.2rem;
}

.pager_content{
      width: 100%;
      height: 100%;
      background: var(--dark-400);
      opacity: 0;
      visibility: hidden;
}

.pager_content.show{
      animation: show_page .3s ease-in-out forwards;
}

@keyframes show_page{
      100%{
            opacity: 1;
            visibility: visible;
      }
}

.pager_banner{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
}

.pager_banner img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.pager_banner:before{
      width: 100%;
      height: 100%;
      background: linear-gradient(rgba(35,34,40,.3), var(--dark-400) 80%);
      top: 0;
      left: 0;
}

.pager_container{
      position: relative;
      width: 100%;
      height: 100%;
}

.header_page{
      width: 100%;
      height: 70px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--gradient-45);
      margin-top: .1rem;
}

.header_page span{
      text-transform: uppercase;
      position: relative;
      line-height: 20px;
      font-size: 5rem;
      font-family: var(--ft-4);
      color: rgba(0,0,0,.5);
}

.header_page span:before,
.header_page span:after{
      width: 10px;
      height: 10px;
      background: var(--light);
      top: calc(50% - .5px);
}

.header_page span:before{
      left: -10px;
      transform: translateX(-100%)
}

.header_page span:after{
      right: -10px;
      transform: translateX(100%);
}

.section_page{
      width: 100%;
      height: 584px;
      padding: 3rem;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--dark-500) var(--theme);
}