.wrapper_relation{
      width: 280px;
      height: 410px;
      position: relative;
      background: var(--dark-300);
      display: flex;
      flex-direction: column;
      align-items: center;
      border-radius: .6rem;
      box-shadow: 0 0 50px rgba(0,0,0,.4);
      padding: 3rem;
      transform: translateY(-4rem);
      opacity: 0;
      visibility: hidden;
}

.wrapper_relation.show{
      animation: bounce .5s ease-in-out forwards;
}

@keyframes bounce{
      0%{
            opacity: 0;
            visibility: hidden;
            transform: translateY(-4rem);
      }
      60%{
            opacity: 1;
            visibility: visible;
            transform: translateY(4rem);
      }
      100%{
            opacity: 1;
            visibility: visible;
            transform: translateY(0);
      }
}

.wrapper_relation:before{
      width: 24px;
      height: 24px;
      background: var(--dark-300);
      left: calc(50% - 12px);
      bottom: -12px;
      transform: rotate(45deg);
}

.close_relation{
      width: 32px;
      height: 32px;
      border-radius: 50%;
      position: absolute;
      top: 2rem;
      right: 2rem;
      background: var(--dark-400);
}

.close_relation *{
      font-size: 2rem;
      color: var(--dark-100);
}

.relation_avatar{
      width: 140px;
      height: 140px;
      border-radius: 50%;
      overflow: hidden;
      margin: 1rem;
      box-shadow: 0 0 0 10px var(--alpha), 0 0 0 20px var(--alpha);
      flex-shrink: 0;
}

.relation_avatar img{
      width: 140px;
      height: 140px;
}

.wrapper_relation h2{
      font-size: 2rem;
      text-transform: uppercase;
      margin-top: 1.5rem;
}

.wrapper_relation span,
.wrapper_relation a{
      margin-top: .75rem;
      background: var(--dark-400);
      padding: .5rem 1.5rem;
      border-radius: 999px;
      text-transform: uppercase;
      color: var(--grys-100);
}

.relation_about{
      margin-top: 1rem;
      height: 80px;
      overflow-y: auto;
      overflow-x: hidden;
}

.relation_about p{
      text-align: center;
      color: var(--grys-100);
      font-size: 1.2rem;
}