.section_page.friend{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      position: relative;
      z-index: 2;
}

.card_friend{
      width: 144px;
      height: 196px;
      position: relative;
      border-radius: .3rem;
      overflow: hidden;
      padding: .3rem;
      margin-bottom: 2rem;
}

.card_friend:before{
      width: 200%;
      height: 200%;
      background: var(--gradient-90);
      z-index: -2;
      top: 50%;
      left: 50%;
      visibility: hidden;
      transform: translate(-50%, -50%) rotate(0);
}

.card_friend:hover:before{
      visibility: visible;
      animation: pin 1s linear infinite;
}

.card_friend:after{
      width: calc(100% - .2rem);
      height: calc(100% - .2rem);
      background: var(--dark-400);
      top: .1rem;
      left: .1rem;
      border-radius: .3rem;
      z-index: -1;
}

@keyframes pin{
      100%{
            transform: translate(-50%, -50%) rotate(360deg);
      }
}

.card_friend__inner{
      width: 100%;
      height: 100%;
      position: relative;
      border-radius: .4rem;
      background: var(--dark-400);
      overflow: hidden;
      display: flex;
      flex-direction: column;
      align-items: center;
}

.card_friend__inner:before{
      width: 100%;
      height: 100%;
      background: linear-gradient(rgba(52,51,58,.3),rgba(52,51,58,1) 80%);
      top: 0;
      left: 0;
      z-index: 1;
}

.card_friend__inner img{
      width: 144px;
      min-width: 100%;
      height: 196px;
      min-height: 100%;
      position: absolute;
}

.card_friend__inner span,
.card_friend__inner a{
      position: relative;
      z-index: 2;
}

.card_friend__inner span{
      margin-top: 120px;
      font-size: 1.4rem;
      font-weight: 500;
}

.card_friend__inner a{
      margin-top: 1rem;
      padding: .5rem 1.5rem;
      text-transform: uppercase;
      background: var(--dark-500);
      color: var(--grys-200);
      border-radius: 999px;
}