.empty_content{
      width: 100%;
      height: 100%;
      min-height: 300px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      gap: 2rem;
}

.empty_icon *{
      font-size: 8rem;
      color: var(--dark-300);
}

.empty_content h3{
      font-size: 2rem;
      color: var(--dark-300);
}