.btn_social{
      width: 48px;
      height: 48px;
      background: rgba(255,255,255,.05);
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      -webkit-backdrop-filter: blur(15px);
      -moz-backdrop-filter: blur(15px);
      backdrop-filter: blur(15px);
      box-shadow: 0 0 15px rgba(0,0,0,.2);
      position: relative;
      overflow: hidden;
}

.btn_social:before{
      width: 60px;
      height: 40px;
      top: 15px;
      left: -20px;
      background: rgba(255,255,255,.1);
      -webkit-backdrop-filter: blur(4px);
      -moz-backdrop-filter: blur(4px);
      backdrop-filter: blur(4px);
      transform: rotate(45deg);
      z-index: 2;
      filter: brightness(200%)
}

.btn_social *{
      font-size: 2.6rem;
}

.btn_social.facebook *{
      color: var(--facebook);
}

.btn_social.twitter *{
      color: var(--twitter);
}

.btn_social.instagram *{
      color: var(--instagram);
}

.btn_social.youtube *{
      color: var(--youtube);
}

.btn_social.tiktok *{

      color: var(--tiktok);
}

.btn_social.tumblr *{
      color: var(--tumblr);
}

.btn_social.discord *{
      color: var(--discord);
}