.wrapper_home{
      width: 100%;
      height: 100%;
      position: relative;
}

.home_header{
      width: 100%;
      height: 450px;
      display: grid;
      grid-template-columns: 210px 410px;
      gap: 2rem;
      padding: 0 3rem;
}

.home_header__aside{
      width: 100%;
      display: flex;
      align-items: center;
      flex-direction: column;
      margin-top: 6rem;
}

.home_aside__avatar{
      width: 150px;
      height: 150px;
      margin: 3rem;
      border-radius: 50%;
      background: var(--dark-400);
      overflow: hidden;
      box-shadow: 0 0 0 10px rgba(52,51,55,.3), 0 0 0 20px rgba(52,51,55,.3), 0 0 0 30px rgba(52,51,55,.3);
}

.home_aside__avatar img{
      width: 150px;
      height: 150px;
      border-radius: 50%;
}

.home_aside__menu{
      width: 150px;
      height: 38px;
      background: var(--dark-300);
      border-radius: 38px;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 1.5rem;
      margin-top: 5rem;
}

.home_aside__menu a{
      width: 32px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      position: relative;
}

.home_aside__menu a *{
      font-size: 2.2rem;
      color: var(--dark-100);
}

.home_aside__menu a:before{
      content: attr(data-tooltip);
      color: var(--dark-100);
      background: var(--dark-500);
      border-radius: .3rem;
      padding: .3rem 1rem;
      text-transform: uppercase;
      top: -3rem;
      left: 50%;
      transform: translateX(-50%);
      opacity: 0;
      visibility: hidden;
      transition: .2s;
}

.home_aside__menu a:hover:before{
      opacity: 1;
      visibility: visible;
}

.home_header__section{
      margin-top: 8rem;
}

.user_data__nick{
      position: relative;
}

.user_data__nick h2{
      font-size: 3rem;
}

.user_data__nick span{
      position: relative;
      padding: .5rem 1.2rem;
      background: var(--alpha);
      border-radius: 999px;
      color: var(--light);
      transform: translate(.5rem, -2rem);

}

.home_user__data p{

      font-size: 1.2rem;
}

.location{
      height: 36px;
      display: flex;
      align-items: baseline;
      gap: .5rem;
      margin-top: 1rem;
}

.location *{
      font-size: 2rem;
      color: var(--theme);
}

.location span{
      font-size: 1.4rem;
      color: var(--light);
      font-weight: bold;
}

.home_user__about{
      margin-top: 6rem;
      height: 150px;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--dark-500) var(--theme);
}

.home_user__about p{
      color: var(--grys-100);
      font-size: 1.4rem;
}

.relation_content{
      position: absolute;
      width: 280px;
      height: 420px;
      bottom: 214px;
      left: calc(50% - 140px);
      display: none;
}

.relation_content.show{
      display: block;
}

.relation_show{
      width: 170px;
      height: 36px;
      display: flex;
      align-items: center;
      gap: 1rem;
      justify-content: center;
      margin-left: calc(50% - 85px);
      margin-top: 2rem;
      border-radius: 36px;
      background: var(--dark-300);
}

.relation_show *{
      color: var(--red);
      font-size: 1.6rem;
}

.relation_show span{
      text-transform: uppercase;
      font-size: 1.1rem;
      font-weight: 500;
      color: var(--grys-100);
}

.home_networks{
      width: 410px;
      height: auto;
      margin-top: 2rem;
      margin-left: calc(50% - 205px);
      display: flex;
      align-items: center;
      flex-direction: column;
}

.networks_title{
      height: 20px;
      position: relative;
      width: 100%;
      display: flex;
      justify-content: center;
}

.networks_title:before,
.networks_title:after{
      width: 100px;
      height: 1px;
      background: rgba(255,255,255,.1);
      top: calc(50% - .5px);
}

.networks_title:before{
      left: 4rem;
}

.networks_title:after{
      right: 4rem;
}

.networks_title span{
      text-transform: uppercase;
      color: var(--grys-100);
      font-weight: 500;
      line-height: 20px;
}

.networs_menu{
      width: 100%;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: space-around;
      margin-top: 1.5rem;
}