.section_page.video{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      position: relative;
      z-index: 2;
}

.video_title{
      width: 600px;
      height: 60px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-top: solid 1px var(--dark-300);
      border-bottom: solid 1px var(--dark-300);
}

.video_title span{
      text-transform: uppercase;
      letter-spacing: .2rem;
      color: var(--dark-100);
}

.video_box{
      width: 600px;
      height: 349px;
      border-bottom: solid 1rem var(--dark-400);
      position: relative;
      background: var(--dark-500);
      margin-top: 3rem;
}

.video_box iframe{
      border: none;
}