.wrapper_online{
      width: 100%;
      height: 100%;
      display: flex;
      gap: 1rem;
      align-items: flex-start;
}

.online_avatar{
      width: 38px;
      height: 38px;
      position: relative;
      border-radius: 50%;
      background: var(--dark-400);
      flex-shrink: 0;
}

.online_avatar img{
      width: 38px;
      height: 38px;
      border-radius: 50%;
}

.online_dots{
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background: var(--dark-200);
      top: 0;
      right: 0;
      position: absolute;
}

.online_dots.green{
      background: var(--green);
}

.online_dots.red{
      background: var(--red);
}

.wrapper_online span{
      flex: 1;
      text-align: center;
      padding: .5rem 0;
      border-radius: 999px;
      background: var(--dark-400);
      text-transform: uppercase;
      font-weight: 400;
      color: var(--dark-100);
}